.cardLocalizacao {
	background: var(--cor-blackTransparent);
	border-radius: 5px;
	color: var(--cor-branco);
	align-items: top;
	display: flex;
	margin: 13rem 5rem 10rem 2rem;
	height: 40rem;
}

.cardTitulo {
	flex-direction: column;
	align-items: center;
	display: flex;
	padding: 2rem 1rem 0 0;
	width: 50%;
	height: 30%;
}

.cardMapa {
	display: flex;
	height: 100%;
	width: 100%;
}

.mapaContato {
	margin: 10px 10px 10px 0;
	border-radius: 5px;
	width: 100%;
}

.cardRefencias {
	margin: 10px 10px 5px 0;
	width: 100%;
}

.mapaRefencia {
	border-radius: 5px;
	width: 100%;
	height: 49%;
}

/* parte do card de mensagem */
.cardMensagem {
	background: var(--cor-blackTransparent);
	margin: 0 2rem 10rem 2rem;
	flex-direction: column;
	align-items: center;
	display: flex;
}

.contatoMensagem {
	justify-content: center;
	flex-direction: column;
	align-items: center;
	display: flex;
	width: 100%;
	color: var(--cor-branco);
}

.mensagemTitulo {
	text-decoration: underline;
	margin: 10px 0 5px 0;
}
.obs {
	color: rgb(172, 23, 23);
}

.mensagemDados {
	flex-direction: column;
	align-items: center;
	display: flex;
	width: 100%;
}

.mensagem {
	box-shadow: inset 0 0 5px var(--cor-branco);
	border: 2px solid var(--cor-branco);
	background: var(--cor-blackTransparent);
	padding: 0.5% 25% 0 0;
	margin: 5px 0 5px 0;
	font-size: 20px;
	color: var(--cor-branco);
}

.enviarMensagem {
	border: 2px solid var(--cor-branco);
	background: var(--cor-red);
	margin: 1rem 0 1rem 0;
	font-weight: bolder;
	font-size: 20px;
}

.enviarMensagem:hover{
	background: var(--cor-vermelhoClaro);
}

@media (max-width: 900px) {
	.cardLocalizacao {
		margin: 13rem 1rem 8rem 1rem;
		height: 30rem;
	}
	.cardTitulo {
		padding: 1rem 0 0 5px;
		width: 60%;
	}
	.cardTitulo h3{
		padding: 0 0.5rem 0 0.5rem;
		font-size: 0.75rem;
	}
	.cardTitulo span{
		padding: 0 0 0 0.5rem;
		font-size: 12px;
	}
	.mapaContato {
		display: none;
	}
	.mapaRefencia {
		margin: 0 0 0 0;
	}
	.mensagemTitulo{
		padding: 0 0 0 0.5rem;
		font-size: 17px;
	}
	.obs{
		padding: 0;
		font-size: 13px;
	}
	.mensagem {
		margin: 1rem 0 0.5rem 0;
		width: 50%;
	}
}
